import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";
import Landingpage from "./pages/home/landingpage";
import Notfound from "./pages/404page/404page";
import "./App.css";
import Blog from "./pages/blog/blog";
import Blogpost from "./pages/blogpost/blogpost";
import { StateProvider } from "./stateprovider";

const App = () => {
  const initialState = {
    theme: { primary: "green" },
    posts: [],
    singlepostdata: [],
    blogviews: 0,
  };

  const reducer = (state, action) => {
    switch (action.type) {
      case "changeTheme":
        return {
          ...state,
          theme: action.newTheme,
        };
      case "addPosts":
        return {
          ...state,
          posts: action.posts,
        };
      case "addBlogviews":
        return {
          ...state,
          blogviews: action.blogviews,
        };

      case "addSinglepost":
        return {
          ...state,
          singlepostdata: [...state.singlepostdata, action.postdata],
        };
      case "addComment":
        return {
          ...state,
          singlepostdata: state.singlepostdata.map((ele) => {
            if (ele.data._id === action.data.postid) {
              ele.comments = action.data.newcomments;
              return ele;
            }
            return ele;
          }),
        };

      default:
        return state;
    }
  };
  return (
    <StateProvider initialState={initialState} reducer={reducer}>
      <HashRouter>
        <Switch>
          <Route exact path="/" component={Landingpage} />
          <Route path="/blog" component={Blog} />
          <Route path="/post/:id" component={Blogpost} />
          <Route path="404" component={Notfound} />
          <Route path="*" component={Notfound} />
        </Switch>
      </HashRouter>
    </StateProvider>
  );
};

export default App;
