import React, { useState, useEffect } from "react";
import { Offline, Online, Detector } from "react-detect-offline";
import Card from "../../components/card/card";
import Navbar from "../../components/Navbar/navbar";
import Pageloader from "../../components/loader/pageloader";
import "./blog.css";
import { useStateValue } from "../../stateprovider";
import Footerblogpost from "../../components/footer/footerblogpost";
import Axios from "axios";
// const API = "http://localhost:3001";
const API = "https://blogrizky.herokuapp.com";

export default function Blog(props) {
  const [{ posts, blogviews }, dispatch] = useStateValue();

  // console.log(posts);

  const [onceoffline, setonceoffline] = useState(false);
  const [isloading, setisloading] = useState(true);
  const [timediff, settimediff] = useState(0);
  const [timeoutsync, settimeoutsync] = useState(true);

  // const getpost = () => {
  //   return new Promise((resolve, reject) => {
  //     var postarr = [];
  //     entries.forEach(element => {
  //       const mdfile = require(`../../entries/${element.path}.md`);
  //       postarr.push(fetch(mdfile));
  //     });
  //     console.log("postss");
  //     resolve(postarr);
  //   });
  // };

  //   var callMyPromise = async () => {
  //     var result = await getpost();
  //     //anything here is executed after result is resolved
  //     return result;
  //   };

  useEffect(() => {
    // callMyPromise()
    setisloading(false);
    fetch("https://worldtimeapi.org/api/ip")
      .then((res) => res.json())
      .then((result) => {
        // console.log(result.unixtime);

        var timeDiff = Math.abs(Date.now() / 1000 - result.unixtime);
        // console.log("timediff", timeDiff);
        settimediff(timeDiff / 60);
        if (timeDiff > 600) {
          settimeoutsync(false);
          // console.log("Data appears to be " + timeDiff / 60 + " minutes old.");
        }
      })
      .catch((err) => {
        // console.log(err);
      });

    if (blogviews === 0) {
      Axios.get(`${API}/api/blog`, {
        location: window.location.href,
      })
        .then((result) => {
          if (result.data.err === false) {
            // console.log("\n\n\n\n");
            // console.log(result.data);
            dispatch({
              type: "addBlogviews",
              blogviews: parseInt(result.data.blogviews),
            });
            // setblogviews(result.data.blogviews);
          }
        })
        .catch((err) => {
          // console.log(err);
        });
    }

    if (!posts.length) {
      console.log("gettingposts no cache");
      Axios.get(`${API}/api/getposts`)
        .then((res) => {
          setisloading(true);
          console.log(res.data);

          // console.log("aaaaa");
          // console.log(result);
          dispatch({ type: "addPosts", posts: res.data });
          console.log("got post added to  cache");
          // setData(pre => {
          //   return [
          //     ...pre,
          //     {
          //       title: sigle.title,
          //       description: String(sigle.description).slice(0, 200),
          //       postedon: sigle.postedon
          //     }
          //   ];
          // });
        })
        .catch((err) => {
          setisloading(true);
          // console.log(err);
        });
    } else {
      setisloading(true);
      console.log("serving from  cache");
    }

    // getpost()
    //   .then(result => {
    //     // console.log(result);
    //     var pss = Promise.all(result);
    //     pss
    //       .then(function(results) {
    //         console.log("my promise");
    //         console.log(results);
    // result.map((sigle, index) => {
    //   sigle
    //     .then(ress => ress.text())
    //     .then(result => {
    //       console.log("aaaaa");
    //       // console.log(result);
    //       setData(pre => {
    //         return [
    //           ...pre,
    //           {
    //             title: entries[index].title,
    //             description: String(result).slice(0, 200),
    //             postedon: entries[index].postedon
    //           }
    //         ];
    //       });
    //     })
    //     .catch(err => {
    //       console.log(err);
    //     });
    // });
    //         //
    //         //   res.json(result);
    //       })
    //       .catch(err => {
    //         console.log(err);
    //       });
    //   })
    //   .catch(err => {});

    // getpost()
    //   .then(data => {
    //     console.log(data);
    //     setData(data);
    //   })
    //   .catch(err => console.log(err));
  }, []);

  return (
    <div className="maindiv">
      <Navbar />
      <Detector
        onChange={(con) => {
          // console.log("con connect;assdkak");
          // console.log(con);
          setonceoffline(true);
        }}
        render={({ online }) => <div></div>}
      />
      <Offline>
        <div className="offlinediv">You appears to be offline</div>
      </Offline>
      <Online>
        <div hidden={!onceoffline} className="onlinediv">
          Welcome Back
        </div>
      </Online>
      <div className="timesync" hidden={timeoutsync}>
        <a href="https://time.is" target="_blank">
          your time appears to be {Math.floor(timediff)} minutes off . Is your
          clock out of sync?
        </a>
      </div>

      <Pageloader isloading={isloading} />

      <div className="container">
        <div className="row">
          <div className="col-md-8">
            <h1 className="my-4">
              Blogged by &ensp;
              <span className="smlltextblogheading">Rizky</span>
            </h1>
            {/* {console.log("data", Data[0])} */}
            {posts.map((element, index) => {
              return (
                <Card
                  key={element._id}
                  title={element.title}
                  description={element.description + "...."}
                  postedon={element.date}
                  index={element._id}
                />
              );
            })}
            {/* <ul class="pagination justify-content-center mb-4">
              <li class="page-item">
                <a class="page-link" href="#">
                  &larr; Older
                </a>
              </li>
              <li class="page-item disabled">
                <a class="page-link" href="#">
                  Newer &rarr;
                </a>
              </li>
            </ul> */}
          </div>
          <div className="col-lg-3">
            <div className="card my-4 commentsection">
              <h5 className="card-header">Total views All time</h5>
              <div className="card-body">
                <div className="input-group">
                  <h5>{blogviews}</h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footerblogpost />
    </div>
  );
}
