import React, { useState, useEffect } from "react";
import Markdown from "markdown-to-jsx";
import "./blogpost.css";
import Gist from "./gist";
import { Offline, Online, Detector } from "react-detect-offline";
import Footerblogpost from "../../components/footer/footerblogpost";
import Axios from "axios";
import Navbar from "../../components/Navbar/navbar";
import Modal from "react-responsive-modal";
import Pageloader from "../../components/loader/pageloader";
import { useStateValue } from "../../stateprovider";
const moments = require("moment/moment");
const API = "https://blogrizky.herokuapp.com";
// const API = "http://localhost:3001";
export default function Blogpost(props) {
  // const [Data, setData] = useState([]);

  const [{ singlepostdata }, dispatch] = useStateValue();

  const [markdown, setmarkdown] = useState("");
  const [postdata, setpostdata] = useState({});
  const [open, setopen] = useState(false);
  const [comment, setcomment] = useState("");
  const [displayname, setdisplayname] = useState("");
  const [email, setemail] = useState("");
  const [postid, setpostid] = useState("");
  const [comments, setcomments] = useState([]);
  const [isloading, setisloading] = useState(true);

  const [onceoffline, setonceoffline] = useState(false);

  const checkpostinstore = () => {
    const id = props.match.params.id;

    for (let index = 0; index < singlepostdata.length; index++) {
      const element = singlepostdata[index];

      if (element.data._id === id) {
        console.log("found " + id);
        return [false, index];
      }
    }
    console.log("not found");
    return [true, null];
  };

  useEffect(() => {
    setisloading(false);

    // console.log(singlepostdata);

    const [found, postid] = checkpostinstore();

    // console.log(checkpostinstore());
    const id = props.match.params.id;
    setpostid(id);
    if (found) {
      const id = props.match.params.id;

      Axios.get(`${API}/api/getpost/${id}`)
        .then((result2) => {
          setpostdata(result2.data);
          Axios.get(result2.data.url)
            .then((s3obj) => {
              setisloading(true);

              setmarkdown(s3obj.data);

              Axios.get(`${API}/api/getcomments/${id}`)
                .then((result3) => {
                  setcomments(result3.data);

                  var singlepostdata = {
                    markdown: s3obj.data,
                    data: result2.data,
                    comments: result3.data,
                  };

                  dispatch({ type: "addSinglepost", postdata: singlepostdata });
                  console.log("added to store");
                })
                .catch((err) => {
                  setisloading(true);
                  // console.log(err);
                });
            })
            .catch((err) => {
              // console.log(err);
              props.history.push("/404");
            });
        })
        .catch((err) => {
          setisloading(true);
          props.history.push("/404");
        });
    } else {
      setisloading(true);
      setpostdata(singlepostdata[postid].data);
      setcomments(singlepostdata[postid].comments);
      setmarkdown(singlepostdata[postid].markdown);
    }
  }, []);

  const commentHandle = (e) => {
    e.preventDefault();

    var payload = {
      displayName: displayname,
      email: email,
      content: comment,
      postid: postid,
    };
    Axios.post(`${API}/api/newcomment`, payload)
      .then((result) => {
        // console.log(result.data);
        setcomment("");
        if (result.data.err === false) {
          onCloseModal();
          Axios.get(`${API}/api/getcomments/${postid}`)
            .then((result) => {
              // console.log("comment data");
              // console.log(result.data);
              setcomments(result.data);

              dispatch({
                type: "addComment",
                data: { postid: postid, newcomments: result.data },
              });
            })
            .catch((err) => {
              // console.log(err);
            });
        }
      })
      .catch((err) => {
        // console.log(err);
      });
  };

  const onOpenModal = (e) => {
    e.preventDefault();
    setopen(true);
  };

  const onCloseModal = () => {
    setopen(false);
  };

  return (
    <div className="blogpostcontainer">
      <Navbar />
      <Detector
        onChange={(con) => {
          // console.log("con connect;assdkak");
          // console.log(con);
          setonceoffline(true);
        }}
        render={({ online }) => <div></div>}
      />
      <Offline>
        <div className="offlinediv">You appears to be offline</div>
      </Offline>
      <Online>
        <div hidden={!onceoffline} className="onlinediv">
          Welcome Back
        </div>
      </Online>
      <Pageloader isloading={isloading} />
      <Modal
        // className="commentmodal"
        open={open}
        onClose={onCloseModal}
        closeIconSize={20}
        styles={{ modal: { color: "black", width: "50%" } }}
        center
      >
        <h4>i'd love to here your comments</h4>
        <form onSubmit={commentHandle}>
          <div class="form-group">
            <input
              type="text"
              required
              placeholder="your name"
              class="form-control"
              onChange={(e) => setdisplayname(e.target.value)}
            ></input>
          </div>
          <div class="form-group">
            <input
              class="form-control"
              type="email"
              required
              placeholder="your email "
              onChange={(e) => setemail(e.target.value)}
            ></input>
          </div>

          <button className="btn btn-primary" type="submit">
            Comment
          </button>
        </form>
      </Modal>
      <div class="container ">
        <div class="row">
          <div class="col-lg-9">
            <h1 class="mt-4">{postdata.title}</h1>

            <hr />
            {/* {console.log(new Date().toISOString())} */}
            <p>Posted {moments(postdata.date).fromNow()} </p>

            <hr />

            <Markdown
              children={markdown}
              options={{
                overrides: {
                  GithubGist: {
                    component: Gist,
                  },
                },
              }}
            />

            <div class="card my-4 commentsection text-white ">
              <h5 class="card-header  ">Leave a Comment:</h5>
              <div class="card-body ">
                <form onSubmit={onOpenModal}>
                  <div class="form-group">
                    <textarea
                      class="form-control"
                      rows="3"
                      required
                      value={comment}
                      onChange={(e) => setcomment(e.target.value)}
                    ></textarea>
                  </div>
                  <button type="submit" class="btn btn-primary">
                    Comment
                  </button>
                </form>
              </div>
            </div>

            {comments.map((ele, index) => {
              // console.log("brrrrrrrrrrrr");
              return (
                <div class="media mb-4">
                  <img
                    class="d-flex mr-3 rounded-circle"
                    // src="https://secure.gravatar.com/avatar/446d1e11e51072cd5a0c4e0daaf2de85?s=160&d=retro&r=pg"
                    src="https://img.icons8.com/pastel-glyph/64/000000/person-male.png"
                    alt=""
                  />
                  <div class="media-body">
                    <div className="commentheaderdiv">
                      <h3 class="mt-0">{ele.displayName}</h3>{" "}
                      <span className="commentfromnow">
                        {" "}
                        {moments(ele.date).fromNow()}{" "}
                      </span>
                    </div>

                    {ele.content}
                  </div>
                </div>
              );
            })}
          </div>

          <div class="col-lg-3">
            <div class="card my-4 commentsection">
              <h5 class="card-header">Views</h5>
              <div class="card-body">
                <div class="input-group">
                  <h5>{postdata.count}</h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footerblogpost />
    </div>
  );
}
